import React, { useRef, useState } from 'react'
import { Label } from '../../commanComponent'
import { useCustomTranslation } from '../../../translation'
import { CustomButton } from '../../globalComponents'

const BulkUploadEmployee = (props) => {
    const { handleOpenClose, openModel,bulkUpdateEmployeeData } = props
    const {t}=useCustomTranslation()
    const [policyFile, setPolicyFile] = useState('')
    const fileInputRef = useRef(null);

    const handlePolcyfile = (e) => {
        const file = e.target.files[0]
        setPolicyFile(file)
    }

    const onSubmitData =()=>{
        const formData = new FormData();
        formData.append("file", policyFile);
        bulkUpdateEmployeeData(formData)
          
    }
    return (
        <>
            <div className={`modal fade selectTeamMemeber ${openModel ? 'show' : ''}`} id="selectTeam"
                tabIndex="-1"
                style={{ display: openModel ? 'block' : 'none', }}
                aria-hidden={!openModel}
            >
                <div className="modal-dialog modal-dialog-centered" style={{ maxWidth: "530px" }}>
                    <div className="modal-content rounded-0 border-0">
                        <div className="modal-header">
                            <h1 className="modal-title fs-6 text-primary fw-semibold">
                                Bulk Upload
                            </h1>
                            <button
                                type="button"
                                className="btn-close"
                                data-bs-dismiss="modal"
                                aria-label="Close"
                                onClick={() => handleOpenClose()}
                            />
                        </div>
                        <div className="modal-body">
                            <div className="formPanel row">
                                <div className="row">
                                    <div className="col-md-3">
                                        <div className="form-group mb-3">
                                            <Label text={t("labels.selectFile")} required={true} />
                                        </div>
                                    </div>
                                    <div className="col-md-9">
                                        <div className="form-group mb-3">
                                            <input type="file"
                                                id="policyFile"
                                                name="poster"
                                                className={`form-control `}
                                                onChange={handlePolcyfile}
                                                accept=".xls,.xlsx,.csv"
                                                ref={fileInputRef}
                                            />

                                        </div>
                                    </div>
                                </div>
                            </div>

                        </div>
                        <div className="modal-footer justify-content-end ">
                            <CustomButton
                                classNames="btn btn-oreang btnShadow text-white "
                                text={t("btns.save")}
                                handleClick={()=>bulkUpdateEmployeeData(policyFile)}
                            />
                        </div>
                    </div>

                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default BulkUploadEmployee
