import React, { useMemo, useRef, useState } from 'react'
import CustomTable from '../../reuseableComponents/customTable/CustomTable';
import { getBadgeColor } from '../../../utils/common';
import { useNavigate } from 'react-router-dom';
import { Icon } from '../../commanComponent';
import { useDispatch } from 'react-redux';
import { employeeActions } from '../../../core/resource/actions';
import { CustomButton } from '../../globalComponents';
import BulkUploadEmployee from './BulkUploadEmployee';
import { payrollActions } from '../../../core/resource/actions';
import { Link } from 'react-router-dom';

const EmployeeTable = (props) => {
    const {
        activeTabEmployeesData,
        isLoading,
        currentPage,
        setCurrentPage,
        setActiveTabType,
        handleViewEmployeeDetail,
        toggleEmpOnboardingDetail,
        handleCreateUpdateEmployeeModal,
        allEmployeeListData,
        bulkUpdateEmployeeData,
    } = props
    const [activeTab, setActiveTab] = useState("allEmployees");
    const [isShowModal, setIsShowModal] = useState(false)
    const fileInputRef = useRef(null);
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const handleActiveTab = (type) => {
        setActiveTab(type);
        setActiveTabType(type === "allEmployees" ? "" : type)
        setCurrentPage(1)
    }

    const toggleEmpHistory = (item) => {
        dispatch(employeeActions.selectedEmployeeData(item))
        navigate("employee-history")
    }

    const setSelectedEmpData = (data) => {
            dispatch(payrollActions.selectedEmployeeData(data))
        }

    const allEmployeeField = [
        { key: 'sn', label: 'S.N' },
        { key: 'emp_id', label: 'Emp Id' },
        { key: 'name', label: 'Full Name' },
        { key: 'mobile', label: 'Mobile' },
        { key: 'email', label: 'Email' },
        { key: 'account_status', label: 'Account Status' },
        { key: 'action', label: 'Action' }
    ];

    const scopedSlotsAllEmployee = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),

        account_status: item => (
            <span
                className={`badge ${getBadgeColor(item.account_status)}`}
            >
                {item.account_status}
            </span>
        ),


        action: item => (
            <div className="btn-group actionDrp">
                <button type="button" className="btn dropdown-toggle-split"
                    data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="bi bi-three-dots-vertical"></i>
                </button>
                <ul className="dropdown-menu">
                    <li>
                        <span className="dropdown-item"
                            onClick={() => handleViewEmployeeDetail(item)}
                        >
                            View Detail</span>
                    </li>
                    <li>
                        <span className="dropdown-item"
                            onClick={() => handleCreateUpdateEmployeeModal(item)}
                        >Update</span>
                    </li>

                    <li>
                        <span
                            onClick={() => toggleEmpHistory(item)}
                            className="dropdown-item"
                        >Employee History</span>
                    </li>
                    {/* <li>
                        <span className="dropdown-item"
                            onClick={() => toggleEmpOnboardingDetail(item)}
                        >
                            View Onboarding Detail</span>
                    </li> */}
                    
                    <li>
                        <Link className="dropdown-item" to={`add-employee-salary`}
                            onClick={() => setSelectedEmpData(item)}
                        >
                            Send Offer Letter</Link>
                    </li>




                </ul>
            </div>
        )
    }), [activeTabEmployeesData]);


    const handleButtonClick = () => {
        // Open file selector
        fileInputRef.current.click();
    };

    // const handleFileUpload = (event) => {
    //     const file = event.target.files[0];
    //     if (file) {
    //         const payload = {
    //             file: file
    //         }
    //         bulkUpdateEmployeeData(payload)
    //     }
    // };

    const toggleBulkUpload = () => {
        setIsShowModal(!isShowModal)
    }
    return (
        <>
            <div className="cardBox myRequest mb-3">
                <div className='d-flex justify-content-between'>
                    <div className="cardHead">

                        <img src="images/myrequest-icon.svg" alt="" width="35" />
                        <h3 className="ms-2">Employees</h3>
                    </div>
                    <div>
                        <>
                            <CustomButton
                                classNames={"btn btn-primary gradientBg"}
                                handleClick={toggleBulkUpload}
                                text={"Bulk Upload"}
                                icon={<Icon className={"fa fa-upload"} />}
                            />
                           
                        </>
                    </div>
                </div>
                <ul className="nav nav-tabs leaveTabs mb-3">
                    <li className="nav-item">
                        <button
                            className={`nav-link ${activeTab === "allEmployees" ? "active" : ""}`}
                            data-bs-toggle="tab"
                            data-bs-target="#allEmployees"
                            type="button"
                            onClick={() => handleActiveTab("allEmployees")}
                        >
                            All Employees
                        </button>
                    </li>
                    <li className="nav-item">
                        <button
                            className={`nav-link ${activeTab === "active" ? "active" : ""}`}
                            data-bs-toggle="tab"
                            data-bs-target="#active"
                            type="button"
                            onClick={() => handleActiveTab("active")}
                        >
                            Active Employees
                        </button>
                    </li>
                    <li className="nav-item">
                        <button
                            className={`nav-link ${activeTab === "inactive" ? "active" : ""}`}
                            data-bs-toggle="tab"
                            data-bs-target="#inactive"
                            type="button"
                            onClick={() => handleActiveTab("inactive")}
                        >
                            Inactive Employees
                        </button>
                    </li>
                </ul>
                <div className="tab-content">
                    <div className={`tab-pane fade ${activeTab === "allEmployees" ? "show active" : ""}`} id="allEmployees">
                        <div className="table-responsive tableDiv text-nowrap overflowTable">
                            <CustomTable
                                data={allEmployeeListData?.data}
                                fields={allEmployeeField}
                                isLoading={isLoading}
                                pagination={true}
                                currentPage={currentPage}
                                setCurrentPage={setCurrentPage}
                                totalPages={allEmployeeListData?.totalPages}
                                isHeader={false}
                                tableStyle={{ padding: "0px", boxShadow: "none" }}
                                scopedSlots={scopedSlotsAllEmployee}
                            />

                        </div>
                    </div>
                    <div className={`tab-pane fade ${activeTab === "active" ? "show active" : ""}`} id="active">
                        <div className="table-responsive tableDiv text-nowrap">
                            <CustomTable
                                data={allEmployeeListData?.data}
                                fields={allEmployeeField}
                                isLoading={isLoading}
                                pagination={true}
                                currentPage={currentPage}
                                setCurrentPage={setCurrentPage}
                                totalPages={allEmployeeListData?.totalPages}
                                isHeader={false}
                                tableStyle={{ padding: "0px", boxShadow: "none" }}
                                scopedSlots={scopedSlotsAllEmployee}
                            />
                        </div>
                    </div>
                    <div className={`tab-pane fade ${activeTab === "inactive" ? "show active" : ""}`} id="inactive">
                        <div className="table-responsive tableDiv text-nowrap">
                            <CustomTable
                                data={allEmployeeListData?.data}
                                fields={allEmployeeField}
                                isLoading={isLoading}
                                pagination={true}
                                currentPage={currentPage}
                                setCurrentPage={setCurrentPage}
                                totalPages={allEmployeeListData?.totalPages}
                                isHeader={false}
                                tableStyle={{ padding: "0px", boxShadow: "none" }}
                                scopedSlots={scopedSlotsAllEmployee}
                            />
                        </div>
                    </div>
                    {isShowModal && <BulkUploadEmployee
                        handleOpenClose={toggleBulkUpload}
                        openModel={isShowModal}
                        bulkUpdateEmployeeData={bulkUpdateEmployeeData}
                    />}


                </div>
            </div>
        </>
    )
}

export default EmployeeTable
